var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-container"},[_c('el-breadcrumb',[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首頁")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("服務項目")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("美麗重現")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("韓式釘書機雙眼皮")])],1),_vm._ssrNode(" "),_c('Title',{attrs:{"title":"韓式釘書機雙眼皮","titleEn":"KOREAN DOUBLE EYELID"}}),_vm._ssrNode(" <div class=\"article-container service-page\"><img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/koreanDoubleEyelid/banner.jpg")))+" alt=\"韓式釘書機雙眼皮 Banner\"></div> <div class=\"article-container\"><h2>美麗功效</h2> <p>眼睛是一個人的靈魂之窗，擁有一對明亮動人的雙眸是許多愛美族在意的重點！所以雙眼皮手術一直是詢問度相當高的熱門項目。雙眼皮手術方式大致可以分成縫雙眼皮手及割雙眼皮手術兩種，兩種手術方式均各有其適合的族群，美麗新診所會針對個人的條件及需求給予適合個人的手術建議。</p> <p>在縫雙眼皮手術當中，隨著手術技巧與器械的進步，日式與韓式訂書針雙眼皮相繼推出，也為台灣雙眼皮手術展開了全新的一頁，訂書針雙眼皮由於同時具有傷口極小、恢復期短、維持時間長等優點，所以在近年來蔚為風潮，擁有好看的雙眼皮也變得越來越簡單了。</p> <p>所謂的訂書針式雙眼皮其實並不是真的用釘書機去訂出雙眼皮來，而是指新的雙眼皮縫合法；又可以分成日式訂書針雙眼皮及韓式訂書針雙眼皮兩種手術方式。</p></div> <div class=\"article-container\"><h2>訂書針雙眼皮和一般的雙眼皮手術的區別</h2> <p>訂書針雙眼皮手術主要是結合切割和縫線法的優點，可創造自然深邃的美麗雙眼，是現在最熱門的雙眼皮手術！訂書針雙眼皮手術，運用韓式、日本專利縫針，將上眼瞼以鐮刀狀切割縫合，與舊式的縫雙眼皮，最大差異處在縫釘的技巧與過程。由於使用的是特殊的鐮刀針，能讓施術醫師能較順手些，因此手術成敗往往取決於醫師純熟的專業技術！</p></div> <div class=\"article-container\"><h2>韓式釘書針雙眼皮手術</h2> <p>手術是利用一條專用的不可吸收線(要從韓國專案進口，比睫毛還要細)，將上眼皮的真皮層與皮下的眼瞼板做連續性、來回的縫合，把以往點狀的固定變成一條紮實連續的車縫線(就像縫紉機車衣服一樣，非常的穩固)，創造出一條自然的雙眼皮皺摺，上眼皮只有四個針孔大小的傷口，只要不到一週就可以復原，不論在穩定性或持久性都比傳統的縫線法要來的更好，所以才會在韓國如此受到歡迎。</p></div> <div class=\"article-container\"><h2>訂書針雙眼皮的特色</h2> <p>手術恢復期極短 ，約傳統手術一半時間。 2.出血量極少、不瘀青 、不腫脹，手術後隔天即可正常上班，平均一週內可以全部消腫</p></div> <div class=\"article-container\"><h2>適用對象</h2> <p>適合單眼皮、眼皮脂肪較少、眼皮不鬆垂之族群。</p> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/koreanDoubleEyelid/1.jpg")))+" alt=\"訂做您的 主電眼\"></div> <div class=\"article-container\"><h2>術後小叮嚀</h2> <ul><li>- 不可食用刺激性食物(如：菸、辣椒等)，盡可能不要熬夜。</li> <li>- 術後眼睛盡量向上看(不可抬下巴或挑眉)，此方法可加速消腫，讓雙眼皮更自然，若有疼痛、緊繃的現象則為正常。</li> <li>- 術後仍可正常洗臉，但傷口每天需用生理食鹽水清潔後塗上藥膏，並保持傷口乾燥。PS：術後前三天採冰敷，第四天溫敷。</li> <li>- 術後至拆線期間，需保持傷口乾淨、清潔，避免傷口沾染刺激物(如：洗面乳、洗髮精)。</li> <li>- 術後不可揉眼睛或做會使眼壓升高的行為(如：哭泣、用眼過度)，因易造成腫脹且易延長消腫時間。</li> <li>- 術後可正常生活作息，不影響規律生活。</li> <li>- 睡前應避免攝取過多的水分，也可將枕頭墊高，以減少眼睛腫脹及不適。</li> <li>- 拆線後兩星期回診，並於醫師回診後開始按摩。 9. 術後拆完線兩星期後再戴隱形眼鏡或上眼妝，避免造成眼皮傷害。</li> <li><h4>本療程的效果可能因個人體質的不同而有差異，請務必與醫師溝通諮詢</h4></li></ul></div> <div class=\"article-container\"><h2>美麗案例</h2> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/koreanDoubleEyelid/案例1.png")))+" alt=\"案例1\n        \"> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/koreanDoubleEyelid/案例2.png")))+" alt=\"案例2\n        \"> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/koreanDoubleEyelid/案例3.png")))+" alt=\"案例3\n        \"> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/koreanDoubleEyelid/案例4.png")))+" alt=\"案例4\n        \"> <h4>療程效果會因個人體質而有所差異</h4></div>")],2)}
